
.floating-button{position:relative;z-index:5;-webkit-appearance:none;-moz-appearance:none;appearance:none;cursor:pointer;width:11.875em;background-color:transparent;border:1px solid #FF6266;border-radius:25px;padding:11px 0;transition:background-color 500ms
}
.floating-button::after{content:"";display:block;width:100%;height:100%;position:absolute;z-index:-1;top:0px;background-color:transparent;border-radius:20px;box-shadow:0px 6px 11px -1px rgba(255,149,152,0.65);opacity:0;transition:opacity 450ms ease-in-out
}
.floating-button .label{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;font-family:"Lato",sans-serif;font-weight:300;font-size:1.0em;line-height:1;color:#FF6266;transition:color 400ms
}
.floating-button:hover{background-color:#FF6266
}
.floating-button:hover::after{opacity:1
}
.floating-button:hover .label{color:#F6F6F6 !important
}
