
.section-content{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;position:absolute;width:100%;height:100%
}
.section-content .project-header{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;padding:45px 0 0
}
.section-content .project-header .project-avatar .avatar{font-size:8.642em;color:#515151
}
.section-content .project-header .project-avatar .avatar::before{display:block;line-height:0.95
}
.section-content .project-header .project-label .label{font-weight:100;font-size:4.9em;color:#515151;margin:2px 0 10px
}
.section-content .project-header .project-platform-icon{font-size:.625em;padding:0 7px
}
.section-content .project-description{-ms-flex-positive:1;flex-grow:1;-ms-flex-item-align:end;align-self:flex-end;overflow:scroll;padding:0.3125em 3.125em
}
.section-content .project-description p{font-size:.92em;font-weight:300;line-height:1.4;text-align:center;color:#515151
}
.section-content .project-controls{display:-ms-flexbox;display:flex;-ms-flex-positive:1;flex-grow:1;-ms-flex-pack:center;justify-content:center;padding:15px 0 25px
}
.section-content .project-controls .project-control{margin:0 15px
}



