
.menu-overlay{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;position:fixed;z-index:-20;top:0px;left:0px;width:100vw;height:100vh;background-color:#F6F6F6;opacity:0 !important;transition-property:opacity, z-index;transition-duration:450ms,0s;transition-delay:0s,450ms;transition-timing-function:ease-out
}
.menu-overlay *{transform:scale(2);transition:transform 400ms ease-out
}
.menu-overlay.is-active{opacity:1 !important;z-index:20;transition:opacity 500ms ease-in
}
.menu-overlay.is-active *{transform:scale(1);transition-property:transform;transition-duration:300ms;transition-timing-function:cubic-bezier(0.67, 0.26, 0.74, 0.38)
}
.menu-overlay.is-absolute{position:absolute;width:100%;height:100%
}
.colors-background .menu-overlay .entry-link{color:#F6F6F6 !important
}
.colors-background .menu-overlay .entry-link:hover{color:#F6F6F6 !important
}
.menu-overlay.colors-background .entry-link{color:#F6F6F6 !important
}
.menu-overlay.colors-background .entry-link:hover{color:#F6F6F6 !important
}
.colors-foreground .menu-overlay .entry-link{color:#515151 !important
}
.colors-foreground .menu-overlay .entry-link:hover{color:#515151 !important
}
.menu-overlay.colors-foreground .entry-link{color:#515151 !important
}
.menu-overlay.colors-foreground .entry-link:hover{color:#515151 !important
}
.colors-accent .menu-overlay .entry-link{color:#FF6266 !important
}
.colors-accent .menu-overlay .entry-link:hover{color:#FF6266 !important
}
.menu-overlay.colors-accent .entry-link{color:#FF6266 !important
}
.menu-overlay.colors-accent .entry-link:hover{color:#FF6266 !important
}
.colors-secondary .menu-overlay .entry-link{color:#000 !important
}
.colors-secondary .menu-overlay .entry-link:hover{color:#000 !important
}
.menu-overlay.colors-secondary .entry-link{color:#000 !important
}
.menu-overlay.colors-secondary .entry-link:hover{color:#000 !important
}
.colors-primary .menu-overlay .entry-link{color:#F6F6F6 !important
}
.colors-primary .menu-overlay .entry-link:hover{color:#F6F6F6 !important
}
.menu-overlay.colors-primary .entry-link{color:#F6F6F6 !important
}
.menu-overlay.colors-primary .entry-link:hover{color:#F6F6F6 !important
}
.menu-overlay-entries{margin:0;padding:0
}
.menu-overlay-entries .menu-overlay-entry{position:relative;text-align:center;margin:25px 0
}
.menu-overlay-entries .menu-overlay-entry .entry-link{font-family:"Ailerons-Regular",sans-serif;font-size:33px;color:#FF6266;-webkit-font-smoothing:antialiased !important;-webkit-transform:translateZ(0px) !important;transition:color 200ms
}
.menu-overlay-entries .menu-overlay-entry .entry-link:hover{color:#FF6266
}



